    padding: .7rem;
    min-width: 200px;
    transition: all 0.3s ease-in-out;
    &:active, &:hover {
      background: $blue;
      color: $light-gray;
      transition: all 0.3s ease-in-out;
    }
    @include retina {
      font-size: 3rem;
      padding: 2rem;
    }
  }
}

// 404

.not-found {
  display: flex;
  flex-direction: column;
  h1 {
    width: auto;
    align-self: center;
  }
}